import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, LoadingController, ToastController, Platform} from 'ionic-angular';
import {Camera} from '@ionic-native/camera';
import {Crop} from "@ionic-native/crop";
import {Geolocation} from '@ionic-native/geolocation';
import {BarcodeScanner} from "@ionic-native/barcode-scanner";
import {UserService} from "../../providers/user.service";
import {ArticleService} from "../../providers/article.service";
import { DomSanitizer } from '@angular/platform-browser';

@IonicPage()
@Component({
  selector: 'page-add',
  templateUrl: 'add.html',
})
export class AddPage {

  title:any;
  type:any;
  description:any;
  price:any;
  ean: any;
  brand:any;
  condition:any;

  currency: any;

  errorMessage: string;
  errorMessageTP:any; //TP : Take Picture
  taked_picture: string;
  has_taked_picture: boolean = false;
  readonly DEFAULT_IMAGE: string = 'http://placehold.it/300x300';

  constructor(
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public geolocation: Geolocation,
    public navCtrl: NavController,
    public camera:Camera,
    public crop:Crop,
    public platform: Platform,
    public alertCtrl: AlertController,
    private articleService: ArticleService,
    public userService: UserService,
    public barcodeScanner: BarcodeScanner,
    private sanitizer:DomSanitizer
  ) {
    //this.taked_picture = this.DEFAULT_IMAGE;
    // if (!this.platform.is('cordova')) {
    //   console.warn('Cordova is not available');
    //   const alert = this.alertCtrl.create({
    //     title: 'Attention!',
    //     subTitle: 'Cordova is not available',
    //     buttons: ['Close']
    //   });
    //   alert.present();
    // }
    this.currency = this.userService._profil.currency;
  }

  takePicture() {
      this.camera.getPicture({
          destinationType: this.camera.DestinationType.DATA_URL,
          encodingType: this.camera.EncodingType.JPEG,
          mediaType: this.camera.MediaType.PICTURE,
          quality: 50,
          targetWidth: 1000,
          targetHeight: 1000,
          correctOrientation: true
      }).then((imageData) => {
          // imageData is a base64 encoded string
          this.has_taked_picture = true;
          this.taked_picture     = "data:image/jpeg;base64," + imageData;
      }, (err) => {
          //console.log(err);
          this.errorMessageTP = err;
          const alert = this.alertCtrl.create({
            title: 'Attention!',
            subTitle: 'Only works in real device',
            buttons: ['Close']
          });
          alert.present();
      });
  }

  takeGallery(): Promise<any> {
    // Get Image from ionic-native's built in camera plugin
    return this.camera.getPicture({
      allowEdit: true,
      sourceType: this.camera.PictureSourceType.SAVEDPHOTOALBUM,
      mediaType: this.camera.MediaType.ALLMEDIA,
      destinationType: this.camera.DestinationType.FILE_URI,
      quality: 50,
      targetWidth: 1000,
      targetHeight: 1000,
    })
      .then((fileUri) => {
        // Crop Image, on android this returns something like, '/storage/emulated/0/Android/...'
        // Only giving an android example as ionic-native camera has built in cropping ability
        if (this.platform.is('ios')) {
          return fileUri
        } else if (this.platform.is('android')) {
          // Modify fileUri format, may not always be necessary
          fileUri = 'file://' + fileUri;

          /* Using cordova-plugin-crop starts here */
          return this.crop.crop(fileUri, { quality: 100 });
        }
      })
      .then(
        (path) => {
          // path looks like 'file:///storage/emulated/0/Android/data/com.foo.bar/cache/1477008080626-cropped.jpg?1477008106566'
          this.has_taked_picture = true;
          this.taked_picture     = path;
          //console.log('Cropped Image Path!: ' + path);
          return path;
        },
        (err) => {
          this.errorMessage = err;
          //console.log('Error cropping image', err);
          const alert = this.alertCtrl.create({
            title: 'Error !',
            subTitle: 'Error while taking a picture',
            buttons: ['Close']
          });
          alert.present();
        }
      )
  }

  scanCode() {
    this.barcodeScanner.scan().then(barcodeData => {
      this.ean = barcodeData.text;
    }, (err) => {
      //console.log('Error: ', err);
    });
  }

  add(){
    if(!this.checkForm()) {
      return;
    }

    let loader = this.loadingCtrl.create({content: 'Please wait while publishing your item', duration: 5000});
    loader.present();
    let watch = this.geolocation.getCurrentPosition({ maximumAge: 300000, timeout: 5000, enableHighAccuracy: true });
    watch.then(
      (data) => {
        this.articleService.saveArticle({
          taked_picture: this.taked_picture ? this.taked_picture : this.DEFAULT_IMAGE,
          title: this.title,
          type: this.type,
          description: this.description,
          currency: this.userService._profil.currency,
          price: this.price,
          status: 'published',
          latitude: data.coords.latitude + Math.random() * (0.0019 + 0.0019) - 0.0019,
          longitude: data.coords.longitude + Math.random() * (0.0019 + 0.0019) - 0.0019,
          uid: this.userService._userFirebase.uid,
          brand: this.brand,
          condition: this.condition
        });

        loader.dismissAll();

        let alert = this.alertCtrl.create({
          title: 'Published',
          subTitle: 'Item successful published',
          buttons: ['Close']
        });

        alert.onDidDismiss(() => {
          this.navCtrl.pop();
        });

        alert.present();
      }).catch((error) => {
      this.errorMessage = error;
      //console.log('Error : ', error);
      const alert = this.alertCtrl.create({
        title: 'Error',
        subTitle: 'Error while getting your position',
        buttons: ['Close']
      });
      alert.present();
    });
  }

  save() {
    if(!this.checkForm()) {
      return;
    }

    let loader = this.loadingCtrl.create({content: 'Please wait while saving your item', duration: 10000});
    loader.present();
    this.articleService.saveInventory({
      taked_picture: this.taked_picture,
      title: this.title,
      type: this.type,
      description: this.description,
      price: this.price,
      currency: this.userService._profil.currency,
      ean: this.ean,
      uid: this.userService._userFirebase.uid,
      status: "inventory",
      brand: this.brand,
      condition: this.condition
    });

    loader.dismiss();

    let alert = this.alertCtrl.create({
      title: 'Saved',
      subTitle: 'Item successful saved',
      buttons: ['Close']
    });

    alert.onDidDismiss(() => {
      this.navCtrl.pop();
    });

    alert.present();
  }

  checkForm() {
    if(!this.taked_picture) {
      this.toastCtrl.create({
        message: 'Please add a picture',
        duration: 2000,
        position: 'bottom'
      }).present();
      return false;
    }

    if(!this.title || !this.type || !this.description || !this.price || !this.condition) {
      this.toastCtrl.create({
        message: 'Please fill all fields',
        duration: 2000,
        position: 'bottom'
      }).present();
      return false;
    }

    if(this.price <= 0) {
      const alert = this.alertCtrl.create({
        title: 'Error !',
        subTitle: 'No valid amount',
        buttons: ['Close']
      });
      alert.present();
      return false;
    }

    return true;
  }

}



